module.exports = function (mod) {
  mod
    .config(function ($stateProvider) {
      $stateProvider
        .state('app.playlists', {
          url: '/playlists',
          views: {
            'menuContent': {
              template: require('./playlists.html'),
              controller: 'PlaylistsCtrl as vm'
            }
          }
        })
    })
    .controller('PlaylistsCtrl', function ($scope) {
      $scope.playlists = [
        {title: 'Reggae', id: 1},
        {title: 'Chill', id: 2},
        {title: 'Dubstep', id: 3},
        {title: 'Indie', id: 4},
        {title: 'Rap', id: 5},
        {title: 'Cowbell', id: 6}
      ];
    });
};



